@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components{

    html, body{
        @apply bg-gray-100
    }

    .done{
        @apply bg-blue-100 opacity-50
    }
    .done p{
        @apply line-through
    }

    form{
        @apply w-full mx-auto my-5 flex items-center flex-col
    }

    form h1{
        @apply font-bold text-2xl my-5
    }

    form input{
        @apply w-1/3 mx-auto my-0 border-b-8 border-gray-200 p-5 outline-none rounded-lg shadow-md shadow-gray-300 transition-all
    }

    form input:focus{
        @apply border-violet-300 w-1/2 p-8
    }

    ul{
        @apply w-1/2 mx-auto my-0 break-words
    }

    ul li{
        @apply bg-yellow-100 shadow-sm shadow-gray-300 my-5 rounded-md p-5 flex items-center justify-between
    }

    ul li input.checkbox{
        @apply cursor-pointer
    }

    ul li div{
        @apply flex justify-between items-center
    }

    ul li p{
        @apply break-all mx-5
    }

    ul li span{
        @apply text-gray-500 cursor-pointer 
    }

    .features{
        @apply mx-auto my-10 bg-gray-300 p-5 w-1/3 shadow-md opacity-25
        rounded-md
    }
    
}